import React ,{useState,useEffect} from 'react';
import ToDoList from './toDoList'
import InputForm from './inputForm'


function App() {
  const [item , setItem] =useState('')
    const [todos , setTodos] = useState([])
    const [status , setStatus] =useState('all')
    const [filteredStatus, setFilteredStatus] = useState([])

    useEffect(() => {
        getTodosFromStorage()
    }, []);

    useEffect(()=>{
        filteredTodos();
        saveToLocalStorage();
    },[todos,status]);

    const saveToLocalStorage = () =>{
        localStorage.setItem("todos",JSON.stringify(todos))
        }

    const getTodosFromStorage = () =>{
        if(localStorage.getItem("todos")===null){
        localStorage.setItem("todos",JSON.stringify([]));
        }else{
        let savedTodos = JSON.parse(localStorage.getItem("todos"))
        setTodos(savedTodos)
        }
    }

    const filteredTodos = () =>{
        switch(status){
        case 'Complete':
        setFilteredStatus(todos.filter(todo=>todo.completed===true))
        break;
        case 'Incomplete':
        setFilteredStatus(todos.filter(todo=>todo.completed === false))
        break;
        default:
          setFilteredStatus(todos)
          break;   
    }
  }
  return (
    <div className="container">
       <InputForm  setStatus={setStatus} todos={todos}  setTodos={setTodos} setItem={setItem} item={item}/>
        <ToDoList todos={todos} setTodos={setTodos} filteredStatus={filteredStatus}/>
    </div>
  );
}

export default App;
