import React from 'react';

const InputForm = ({item,setItem,setTodos,todos,setStatus}) =>{

    const getItem = (e) => {
        setItem(e.target.value)
    }

    const addTodoItem = () =>{
        setTodos([
            ...todos , 
            {text:item , completed:false , id:Math.random()},
        ]);
        setItem("")
    };

    const selectStatus= (e) =>{
        setStatus(e.target.value)
    }

    const handleKeyPress = (event) =>{
        if(event.key === 'Enter'){
            addTodoItem()
        }
    }

    return(
        <div className="flex">
        <div className="header">
        <input
        value={item}
        onChange={getItem}
        type="text" 
        placeholder="Add items to your to do list"
        className="input"
        onKeyPress={handleKeyPress}
        >
        </input>
        <button 
        className="button"
        onClick={addTodoItem}
        > Add </button>
        <select 
        onChange={selectStatus}
        className="select">
            <option>All</option>
            <option>Incomplete</option>
            <option>Complete</option>
        </select>
        </div>
        </div>
    );
}

export default InputForm