import React from 'react';
import Todo from './todo'

const ToDoList = ({todos,setTodos,filteredStatus}) =>{

    
console.log(todos)
    return(
        <>
        <div className="flex">
        <div className="item__list">
        <ol>
        {filteredStatus.map(todo=>(
            <Todo 
            key={todo.id} 
            text={todo.text} 
            setTodos={setTodos} 
            todos={todos} 
            todo={todo} 
            />
        ))}
        </ol>
        </div>
        </div>
        </>
    );
}

export default ToDoList;